
.login {
  position: relative;
  width: 100%;
  height: 100%;
}
.login .login-panel {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 300px;
  height: 260px;
  margin: -130px 0 0 -150px;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
}
.login .login-panel .logo-wrapper {
  display: block;
  height: 50px;
  padding: 3px 0;
  background: #4285f4;
  border-radius: 2px 2px 0 0;
  text-align: center;
}
.login .login-panel .user-wrapper {
  width: 260px;
  margin: 30px 20px;
  font-size: 0;
  border-radius: 5px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
}
.login .login-panel .user-wrapper .text {
  display: inline-block;
  width: 80px;
  text-align: center;
  line-height: 30px;
  font-size: 18px;
  color: #fff;
  background: #4285f4;
  border-radius: 5px 0 0 5px;
}
.login .login-panel .user-wrapper #user-name {
  vertical-align: top;
  display: inline-block;
  width: 180px;
  height: 30px;
  line-height: 26px;
  font-size: 16px;
  text-indent: 10px;
  background: #f7f7f7;
  border-radius: 0 5px 5px 0;
  box-sizing: border-box;
}
.login .login-panel .user-wrapper #user-name:focus {
  background: #fff;
  border: 2px solid #4285f4;
  border-left: 0;
  outline: none;
}
.login .login-panel .pwd-wrapper {
  width: 260px;
  margin: 30px 20px;
  font-size: 0;
  border-radius: 5px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
}
.login .login-panel .pwd-wrapper .text {
  display: inline-block;
  width: 80px;
  text-align: center;
  line-height: 30px;
  font-size: 18px;
  color: #fff;
  background: #4285f4;
  border-radius: 5px 0 0 5px;
}
.login .login-panel .pwd-wrapper #user-pwd {
  vertical-align: top;
  display: inline-block;
  width: 180px;
  height: 30px;
  line-height: 30px;
  font-size: 16px;
  text-indent: 10px;
  background: #f7f7f7;
  border-radius: 0 5px 5px 0;
  box-sizing: border-box;
}
.login .login-panel .pwd-wrapper #user-pwd:focus {
  background: #fff;
  border: 2px solid #4285f4;
  border-left: 0;
  outline: none;
}
.login .login-panel .btn-wrapper {
  position: relative;
  margin: 30px 0;
  height: 30px;
  text-align: center;
}
.login .login-panel .btn-wrapper #login-btn {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 50%;
  width: 100px;
  height: 30px;
  margin: 0 0 0 -50px;
  text-align: center;
  line-height: 30px;
  font-size: 18px;
  color: #4285f4;
  background: #f7f7f7;
  border: none;
  border-radius: 5px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.login .login-panel .btn-wrapper #login-btn:hover {
  color: #fff;
  background: #4285f4;
}
.login .login-panel .btn-wrapper .hint {
  position: absolute;
  z-index: 1;
  top: 30px;
  left: 50%;
  width: 80px;
  height: 20px;
  margin: 0 0 0 -40px;
  text-align: center;
  line-height: 20px;
  font-size: 16px;
  color: #f92452;
  background: #f7f7f7;
  border-radius: 0 0 5px 5px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
}
.login .login-panel .btn-wrapper .slip-enter-active,
.login .login-panel .btn-wrapper .slip-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.login .login-panel .btn-wrapper .slip-enter,
.login .login-panel .btn-wrapper .slip-leave-active {
  -webkit-transform: translate(0, -30px);
          transform: translate(0, -30px);
}


