
.bannerset {
  width: 500px;
  padding: 20px;
  margin: 20px auto;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  box-sizing: border-box;
}
.bannerset .img-wrapper img {
  display: block;
  width: 300px;
  margin: 20px auto 0;
  border-radius: 2px;
}
@media (max-width: 667px) {
.bannerset .img-wrapper img {
    width: 100%;
}
}
.bannerset .img-wrapper .input-wrapper {
  position: relative;
  left: 50%;
  width: 100px;
  height: 30px;
  margin: 20px 0 0 -50px;
  text-align: center;
  line-height: 30px;
  font-size: 18px;
  color: #4285f4;
  background: #f7f7f7;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.bannerset .img-wrapper .input-wrapper:hover {
  color: #fff;
  background: #4285f4;
}
.bannerset .img-wrapper .input-wrapper #file {
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 30px;
  opacity: 0;
}
.bannerset .link-wrapper {
  width: 460px;
  height: 30px;
  margin: 20px 0;
  font-size: 0;
  border-radius: 5px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
}
.bannerset .link-wrapper .text {
  display: inline-block;
  width: 80px;
  height: 30px;
  vertical-align: top;
  text-align: center;
  line-height: 30px;
  font-size: 18px;
  color: #fff;
  background: #4285f4;
  border-radius: 5px 0 0 5px;
}
@media (max-width: 667px) {
.bannerset .link-wrapper .text {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 80px;
            flex: 0 0 80px;
}
}
.bannerset .link-wrapper #link {
  display: inline-block;
  width: 380px;
  min-width: 60px;
  height: 30px;
  vertical-align: top;
  line-height: 30px;
  font-size: 16px;
  text-indent: 10px;
  background: #f7f7f7;
  border-radius: 0 5px 5px 0;
  box-sizing: border-box;
}
.bannerset .link-wrapper #link:focus {
  background: #fff;
  border: 2px solid #4285f4;
  border-left: none;
  outline: none;
}
@media (max-width: 667px) {
.bannerset .link-wrapper #link {
    -webkit-box-flex: 1;
        -ms-flex: 1 1 auto;
            flex: 1 1 auto;
}
}
@media (max-width: 667px) {
.bannerset .link-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}
}
.bannerset .btn-wrapper {
  position: relative;
  margin: 30px 0;
  height: 30px;
  text-align: center;
}
.bannerset .btn-wrapper #confirm {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 50%;
  width: 100px;
  height: 30px;
  margin: 0 0 0 -50px;
  text-align: center;
  line-height: 30px;
  font-size: 18px;
  color: #4285f4;
  background: #f7f7f7;
  border: none;
  border-radius: 5px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.bannerset .btn-wrapper #confirm:hover {
  color: #fff;
  background: #4285f4;
}
.bannerset .btn-wrapper .hint {
  position: absolute;
  z-index: 1;
  top: 30px;
  left: 50%;
  width: 80px;
  height: 20px;
  margin: 0 0 0 -40px;
  text-align: center;
  line-height: 20px;
  font-size: 16px;
  color: #f92452;
  background: #f7f7f7;
  border-radius: 0 0 5px 5px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
}
.bannerset .btn-wrapper .slip-enter-active,
.bannerset .btn-wrapper .slip-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.bannerset .btn-wrapper .slip-enter,
.bannerset .btn-wrapper .slip-leave-active {
  -webkit-transform: translate(0, -30px);
          transform: translate(0, -30px);
}
@media (max-width: 667px) {
.bannerset {
    width: 100%;
}
}


