
.loading {
  width: 50px;
  height: 25px;
  font-size: 0;
}
.loading .lt,
.loading .gt {
  position: relative;
  display: inline-block;
  width: 30%;
  height: 100%;
}
.loading .lt {
  -webkit-animation: loading1 0.5s ease infinite alternate;
          animation: loading1 0.5s ease infinite alternate;
}
.loading .lt .line-top {
  position: absolute;
  top: 32%;
  display: inline-block;
  width: 100%;
  height: 10%;
  background: #4285f4;
  border-radius: 5px;
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
}
.loading .lt .line-bottom {
  position: absolute;
  bottom: 32%;
  display: inline-block;
  width: 100%;
  height: 10%;
  background: #4285f4;
  border-radius: 5px;
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
}
.loading .gt {
  -webkit-animation: loading3 0.5s ease infinite alternate;
          animation: loading3 0.5s ease infinite alternate;
}
.loading .gt .line-top {
  position: absolute;
  top: 32%;
  display: inline-block;
  width: 100%;
  height: 10%;
  background: #4285f4;
  border-radius: 5px;
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
}
.loading .gt .line-bottom {
  position: absolute;
  bottom: 32%;
  display: inline-block;
  width: 100%;
  height: 10%;
  background: #4285f4;
  border-radius: 5px;
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
}
.loading .slash {
  display: inline-block;
  width: 30%;
  height: 100%;
  text-align: center;
}
.loading .slash .line {
  display: inline-block;
  width: 18%;
  height: 100%;
  background: #4285f4;
  border-radius: 5px;
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
  -webkit-animation: loading2 1s ease infinite;
          animation: loading2 1s ease infinite;
}
@-webkit-keyframes loading1 {
0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
}
100% {
    -webkit-transform: translate(-10px, 0);
            transform: translate(-10px, 0);
}
}
@keyframes loading1 {
0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
}
100% {
    -webkit-transform: translate(-10px, 0);
            transform: translate(-10px, 0);
}
}
@-webkit-keyframes loading2 {
0% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
}
100% {
    -webkit-transform: rotate(390deg);
            transform: rotate(390deg);
}
}
@keyframes loading2 {
0% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
}
100% {
    -webkit-transform: rotate(390deg);
            transform: rotate(390deg);
}
}
@-webkit-keyframes loading3 {
0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
}
100% {
    -webkit-transform: translate(10px, 0);
            transform: translate(10px, 0);
}
}
@keyframes loading3 {
0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
}
100% {
    -webkit-transform: translate(10px, 0);
            transform: translate(10px, 0);
}
}


