
@import "../../assets/stylus/font-awesome.min.css";
.navitem {
  position: relative;
  display: block;
  height: 40px;
}
.navitem.router-link-active .icon {
  background: #fff;
}
.navitem.router-link-active .icon .fa {
  color: #4285f4;
}
.navitem:hover .icon {
  background: #fff;
}
.navitem:hover .icon .fa {
  color: #4285f4;
}
.navitem:hover .hint {
  left: 50px;
  background: #fff;
}
.navitem .icon {
  position: absolute;
  z-index: 2;
  width: 50px;
  height: 40px;
  text-align: center;
  background: #4285f4;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.navitem .icon .fa {
  line-height: 40px;
  font-size: 20px;
  color: #fff;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.navitem .hint {
  position: absolute;
  z-index: 1;
  left: -50px;
  height: 40px;
  padding: 0 5px;
  text-align: center;
  line-height: 40px;
  font-size: 20px;
  color: #4285f4;
  background: #4285f4;
  border-right: 4px solid #4285f4;
  box-sizing: border-box;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
@media (max-width: 667px) {
.navitem .hint {
    display: none;
}
}


