
.admin {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
}
.admin .nav-wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 50px;
          flex: 0 0 50px;
  height: 100%;
  background: #4285f4;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
@media (max-width: 667px) {
.admin .nav-wrapper {
    position: fixed;
    z-index: 99;
    top: 0;
    left: -50px;
}
}
.admin .main {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 100%;
}
.switch-enter-active,
.switch-leave-active {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.switch-enter {
  position: fixed;
  left: 50px;
  -webkit-transform: translate(0, 100%);
          transform: translate(0, 100%);
}
.switch-leave-active {
  position: fixed !important;
  left: 50px;
  -webkit-transform: translate(0, -100%);
          transform: translate(0, -100%);
}


