
#app {
  width: 100%;
  height: 100%;
  background: url("assets/imgs/e.jpg") no-repeat;
  background-size: 100% 100%;
}
.login-enter-active,
.login-leave-active {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.login-enter {
  position: fixed;
  -webkit-transform: translate(0, 100%);
          transform: translate(0, 100%);
}
.login-leave-active {
  position: fixed;
  -webkit-transform: translate(0, -100%);
          transform: translate(0, -100%);
}


