
@import "../../assets/stylus/font-awesome.min.css";
.profile {
  width: 800px;
  min-height: 150px;
  font-size: 0;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  cursor: pointer;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.profile:hover {
  box-shadow: 0 3px 8px 2px rgba(0,0,0,0.26);
}
.profile .img-wrapper {
  display: inline-block;
  width: 230px;
  height: 130px;
  margin: 10px 0 10px 10px;
  border-radius: 2px;
  overflow: hidden;
}
.profile .img-wrapper img {
  border-radius: 2px;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}
.profile .img-wrapper img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
@media (max-width: 1200px) {
.profile .img-wrapper {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 230px;
            flex: 0 0 230px;
}
}
@media (max-width: 667px) {
.profile .img-wrapper {
    display: none;
}
}
.profile .overview {
  display: inline-block;
  max-width: 540px;
  min-height: 134px;
  margin: 10px;
  vertical-align: top;
}
.profile .overview .title {
  font-size: 20px;
  color: #4285f4;
}
.profile .overview .tag-wrapper {
  width: 100%;
  min-height: 20px;
  padding: 5px 0;
}
.profile .overview .tag-wrapper .tag {
  margin-right: 10px;
}
.profile .overview .text {
  height: 65px;
  overflow: hidden;
}
.profile .overview .text span {
  line-height: 17px;
  font-size: 14px;
}
@media (min-width: 1200px) {
.profile .overview .text img {
    display: none;
}
}
@media (max-width: 1200px) {
.profile .overview .text img {
    display: none;
}
}
@media (max-width: 667px) {
.profile .overview .text img {
    display: inline-block;
    float: left;
    vertical-align: top;
    width: 100px;
    height: 65px;
    margin: 0 10px 0 0;
    border-radius: 2px;
}
}
.profile .overview .meta {
  padding: 5px 0 0 0;
  font-size: 0;
  text-align: right;
}
.profile .overview .meta span {
  display: inline-block;
  margin-right: 10px;
  font-size: 14px;
  color: #7e8c8d;
}
.profile .overview .meta span:hover {
  color: #4285f4;
}
.profile .overview .meta span i {
  display: inline-block;
  margin-right: 4px;
}
.profile .overview .meta span:last-child {
  margin: 0;
}
@media (max-width: 1024px) {
