
.commentitem {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 20px;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
}
.commentitem .avatar {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 50px;
          flex: 0 0 50px;
  height: 50px;
  margin: 0 10px 0 0;
}
.commentitem .avatar img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.commentitem .comment-main {
  vertical-align: top;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  line-height: 14px;
}
.commentitem .comment-main .comment-content .name {
  font-size: 14px;
  color: #4285f4;
}
.commentitem .comment-main .comment-content .text {
  font-size: 14px;
  word-break: break-all;
}
.commentitem .comment-main .comment-time {
  position: absolute;
  right: 20px;
  bottom: 8px;
  text-align: right;
  font-size: 14px;
  color: #7e8c8d;
}


