
.insertpanel {
  position: fixed;
  z-index: 99;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0,0,0,0.4);
  overflow: auto;
}
.insertpanel .insertpanel-main {
  width: 500px;
  margin: 50px auto;
}
@media (max-width: 667px) {
.insertpanel .insertpanel-main {
    width: 100%;
    padding: 0 10px;
    box-sizing: border-box;
}
}
.insertpanel .insertpanel-inner {
  position: relative;
  width: 100%;
  padding: 20px 20px;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  box-sizing: border-box;
}
.insertpanel .insertpanel-inner .close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 20px;
  color: #d1d1d1;
}
.insertpanel .insertpanel-inner .close:hover {
  color: #4285f4;
}
.insertpanel .insertpanel-inner .img-wrapper {
  text-align: center;
  margin: 20px 0 0;
}
.insertpanel .insertpanel-inner .img-wrapper img {
  width: 300px;
  border-radius: 2px;
}
@media (max-width: 667px) {
.insertpanel .insertpanel-inner .img-wrapper img {
    width: 100%;
}
}
.insertpanel .insertpanel-inner .input-wrapper {
  position: relative;
  left: 50%;
  width: 100px;
  height: 30px;
  margin: 20px 0 20px -50px;
  text-align: center;
  line-height: 30px;
  font-size: 18px;
  color: #4285f4;
  background: #f7f7f7;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.insertpanel .insertpanel-inner .input-wrapper:hover {
  color: #fff;
  background: #4285f4;
}
.insertpanel .insertpanel-inner .input-wrapper #file {
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 30px;
  opacity: 0;
}
.insertpanel .insertpanel-inner #confirm {
  display: block;
  width: 100px;
  height: 30px;
  margin: 0 auto;
  font-size: 20px;
  color: #4285f4;
  background: #f7f7f7;
  border: none;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.insertpanel .insertpanel-inner #confirm:hover {
  color: #fff;
  background: #4285f4;
}


