
.banner {
  width: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.banner .load-article {
  width: 920px;
  height: 25px;
  margin: 10px auto;
  padding: 5px 0;
  text-align: center;
  line-height: 25px;
  font-size: 20px;
  color: #4285f4;
  background: #fff;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  border-radius: 2px;
}
.banner .load-article .loading {
  text-align: justify;
  margin: 0 auto;
}
@media (max-width: 1024px) {
.banner .load-article {
    width: 100%;
}
}
@media (max-width: 667px) {
.banner {
    position: relative;
    padding: 0 10px;
    box-sizing: border-box;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
}


