
.notfound {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
}
.notfound .img-wrapper {
  width: 1000px;
  margin: 10px auto 10px;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0px rgba(0,0,0,0.26);
}
.notfound .img-wrapper img {
  width: 100%;
}
@media (max-width: 1200px) {
.notfound .img-wrapper {
    width: 100%;
}
}


