
.comments {
  width: 100%;
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.comments .comments-wrapper {
  width: 100%;
}
.comments .comments-wrapper .push-move {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.comments .comments-wrapper .push-enter-active,
.comments .comments-wrapper .push-leave-active {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.comments .comments-wrapper .push-enter,
.comments .comments-wrapper .push-leave-active {
  opacity: 0;
  -webkit-transform: translate(100%, 0);
          transform: translate(100%, 0);
}
.comments .comments-wrapper .push-leave-active {
  position: absolute;
}
.comments .comments-wrapper .comment-list {
  width: 920px;
  margin: 0 auto;
  font-size: 0;
}
.comments .comments-wrapper .comment-list .commentitem-wrapper {
  display: inline-block;
  width: 800px;
}
.comments .comments-wrapper .comment-list .commentitem-wrapper .commentitem {
  margin: 10px 0;
}
.comments .comments-wrapper .comment-list .btn-wrapper {
  vertical-align: top;
  display: inline-block;
  margin: 10px 0 10px 10px;
}
.comments .comments-wrapper .comment-list .btn-wrapper .delete-btn {
  display: block;
  width: 90px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  font-size: 20px;
  color: #4285f4;
  background: #f7f7f7;
  border: none;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  cursor: pointer;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.comments .comments-wrapper .comment-list .btn-wrapper .delete-btn:hover {
  color: #fff;
  background: #4285f4;
}
.comments .comments-wrapper .comment-list .btn-wrapper .delete-btn:focus {
  outline: none;
}
@media (max-width: 1024px) {
.comments .comments-wrapper .comment-list .btn-wrapper {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 90px;
            flex: 0 0 90px;
}
}
@media (max-width: 1024px) {
.comments .comments-wrapper .comment-list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}
}
.comments .comments-wrapper .load-article {
  width: 920px;
  height: 25px;
  margin: 10px auto;
  padding: 5px 0;
  text-align: center;
  line-height: 25px;
  font-size: 20px;
  color: #4285f4;
  background: #fff;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  border-radius: 2px;
}
.comments .comments-wrapper .load-article .loading {
  text-align: justify;
  margin: 0 auto;
}
@media (max-width: 1024px) {
.comments .comments-wrapper .load-article {
    width: 100%;
}
}
.comments .comments-wrapper .loading-wrapper {
  width: 920px;
  margin: 10px auto 20px;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  cursor: pointer;
}
.comments .comments-wrapper .loading-wrapper .more {
  margin: 0 auto;
  line-height: 35px;
