
.write {
  position: relative;
  width: 100%;
  height: 100%;
  font-size: 0;
}
.write .input-wrapper {
  position: relative;
  vertical-align: top;
  display: inline-block;
  width: 50%;
  height: 100%;
  background: #333;
}
.write .input-wrapper:hover #insert-img-btn {
  display: block;
}
.write .input-wrapper #content {
  width: 100%;
  height: 100%;
  padding: 20px;
  line-height: 1.5;
  font-size: 16px;
  word-wrap: break-word;
  color: #f7f7f7;
  background: #333;
  border: none;
  box-sizing: border-box;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  -webkit-overflow-scrolling: touch;
}
.write .input-wrapper #content:focus {
  outline: none;
}
.write .input-wrapper #insert-img-btn {
  position: absolute;
  display: none;
  width: 30px;
  height: 30px;
  top: 10px;
  right: 10px;
  text-align: center;
  line-height: 30px;
  font-size: 18px;
  color: #4285f4;
  background: #f7f7f7;
  border: none;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  cursor: pointer;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.write .input-wrapper #insert-img-btn:hover {
  color: #fff;
  background: #4285f4;
}
@media (max-width: 667px) {
.write .input-wrapper #insert-img-btn {
    display: block;
    top: 10px;
    right: 80px;
}
}
@media (max-width: 667px) {
.write .input-wrapper {
    width: 100%;
}
}
.write .markdown-wrapper {
  position: relative;
  display: inline-block;
  width: 50%;
  height: 100%;
  background: #fff;
  overflow: auto;
  box-sizing: border-box;
}
.write .markdown-wrapper:hover #create-btn {
  display: block;
}
.write .markdown-wrapper #create-btn {
  position: absolute;
  display: none;
  width: 60px;
  height: 30px;
  top: 10px;
  right: 10px;
  text-align: center;
  line-height: 30px;
  font-size: 18px;
  color: #4285f4;
  background: #f7f7f7;
  border: none;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  cursor: pointer;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.write .markdown-wrapper #create-btn:hover {
  color: #fff;
  background: #4285f4;
}
@media (max-width: 667px) {
.write .markdown-wrapper #create-btn {
    position: absolute;
    display: block;
    z-index: 5;
    top: 10px;
    right: 10px;
}
}
@media (max-width: 667px) {
.write .markdown-wrapper {
    width: 0;
    overflow: visible;
}
.write .markdown-wrapper .markdown {
    display: none;
}
}
.write .insertpanel-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.write .setpanel-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.write .pop-enter-active,
.write .pop-leave-active {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.write .pop-enter-active .insertpanel-inner,
.write .pop-leave-active .insertpanel-inner {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.write .pop-enter-active .setpanel-inner,
.write .pop-leave-active .setpanel-inner {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.write .pop-enter,
.write .pop-leave-active {
  opacity: 0;
}
.write .pop-enter .insertpanel-inner,
.write .pop-leave-active .insertpanel-inner {
  -webkit-transform: translate(0, -200%);
          transform: translate(0, -200%);
}
.write .pop-enter .setpanel-inner,
.write .pop-leave-active .setpanel-inner {
  -webkit-transform: translate(0, -200%);
          transform: translate(0, -200%);
}
@media (max-width: 667px) {
.write {
    position: relative;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
}


